import React, {Component} from 'react';
// import logo from '/src/images/android.webp';
import './App.css';
import './static/css/style.css';
import './static/css/style2.css';
import './static/css/style3.css';
import './static/css/style4.css';
import './static/css/bootstrap.css';
import './static/css/skill.css';
import './static/css/cards.css';
// import './static/css/all.css';
import './static/css/font-awesome.min.css';
import {BrowserRouter} from "react-router-dom";
import Header from './components/header'
import Homenav from "./components/homeNav";
import Personal from './components/personal'
import AboutMe from './components/aboutMe'
import WorkContainer from './containers/WhatIdoContainer'
import SkillContainer from "./containers/SkillContainer";
import ProjectContainer from "./containers/ProjectContainer";
import AchievementContainer from "./containers/AchivementConatainer";
import Contact from './components/contact'
import Footer from "./components/footer";
import ResumePDFViewer from "./components/pdfViewer";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import $ from 'jquery'
// import BounceLoader from 'react-spinners/ClipLoader';
import {BounceLoader, BeatLoader, ScaleLoader} from 'react-spinners';
import LazyLoad from 'react-lazy-load';
import axios from 'axios'
import {css} from '@emotion/core';

// import typeAnimation from "../public/typing";
class App extends Component {

    state = {
        fetch: false,
        loading: true,
        projects: null,
        works: null,
        skills: null,
        programSkill: null,
        hobbies: null,
        achievements: null
    }
    style = {
        display: 'flex'
    }
    style2 = {
        display: 'none'
    }

    componentDidMount() {
        // setTimeout(() => {
        //     this.setState(this.style = {display: 'none'});
        //     this.setState(this.style2 = {display: 'block'})
        // }, 4000)

        if (!this.state.fetch) {
            axios.get('https://portfolio-99b6a.firebaseio.com/.json')
                .then(
                    (result) => {
                        // alert(result.data.whatIdo)
                        this.setState(
                            this.state = {
                                fetch: true,
                                loading: false,
                                projects: result.data.projects,
                                works: result.data.whatIdo,
                                achievements: result.data.achievements,
                                skills: result.data.skills,
                                hobbies: result.data.hobbies,
                                programSkill: result.data.programSkill
                            }
                        )
                        this.setState(this.style = {display: 'none'});
                        this.setState(this.style2 = {display: 'block'})
                    }
                )
        }
    }


    render() {
        console.log(this.state);

        return (


            <div>


                <Router>


                    <BrowserRouter basename={process.env.PUBLIC_URL}>

                        <switch>

                            <Route exact path="/">
                                <div className='loading' style={this.style}>
                                    <ScaleLoader

                                        sizeUnit={"px"}
                                        size={150}
                                        color={'#123abc'}
                                        loading={this.state.loading}
                                    />
                                </div>
                                <div style={this.style2}>
                                    <Header/>
                                    <Personal/>
                                    <AboutMe/>
                                    <WorkContainer works={this.state.works}/>
                                    <LazyLoad offsetVertical={1000}>
                                        <SkillContainer
                                            skills={this.state.skills}
                                            programSkill={this.state.programSkill}
                                            hobbies={this.state.hobbies}
                                        />
                                    </LazyLoad>
                                    <AchievementContainer
                                        projects={this.state.projects}
                                    />
                                    <ProjectContainer
                                        achievements={this.state.achievements}
                                    />

                                    <Contact/>
                                    <Footer/>
                                </div>
                            </Route>

                            <Route exact path="/resume">
                                <div>
                                    <Homenav/>
                                    <div style={{marginTop: "30px"}}>
                                    </div>
                                    <ResumePDFViewer/>
                                </div>

                            </Route>
                        </switch>
                    </BrowserRouter>

                </Router>


            </div>


        );
    }
}

export default App;
