const AchievementContainer  = (props)=>{

    let projectComponent;
    if(props.projects){
        // alert(this.state.projects)
        projectComponent = props.projects.map(
            (project  , index )=>{
                // alert(project);
                return <Achievement
                    key={index}
                    id={index}
                    description = {project.description}
                    details = {project.details}
                    name={project.name}
                    url={project.url}
                    buttonText={project.button}
                    type={project.type}
                />
            }
        )
    }

    return(
        <div style={{background:"#e3e3e3"}} id="achievements">
            <div className="container">
                <div className="jarallax  w3ls-services w3ls-section principles-grids principles-grids1">
                    <h3 className="tittle text-center text-bl mb-sm-5 mb-4">Projects</h3>
                    <div className="">
                        <div className="abt-btm agileits w3layouts aos-init aos-animate" style={{marginTop:"-8em"}}>
                            <div className="row ser-styles ser-styles-res">

                                {projectComponent}

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}


export default AchievementContainer
