const skillContainer = (props)=>{
    var skillComponent,hobbyComponent,programComponent;
    if(props.skills){
        skillComponent  = props.skills.map(
            (skill , index)=>{
                return <Skills
                    key={index}
                    id={index}
                    name={skill.name}
                    percent={skill.percent}
                />
            }
        )
        animateSkills();
    }
    if(props.hobbies){
        hobbyComponent = props.hobbies.map(
            (hobby ,index)=>{
                return <Hobby
                    key={index}
                    id={index}
                    name={hobby.name}
                    icon={hobby.icon}

                />
            }
        )
    }
    if(props.programSkill){
        programComponent = props.programSkill.map(
            (skill ,index)=>{
                return <ProgramSkill
                    key={index}
                    id={index}
                    name={skill.name}
                    percent={skill.percent}

                />
            }
        )
    }

    return(
        <div className="skills-agileinfo" id="skills">
            <div className="container">
                <h3 className="text-center">My skills</h3>
                <div className="modal-spa">
                    <div className="col-md-6 bar-grids bargrids-left">
                        {skillComponent}
                        <p className="p1">A skill set is a particular category of knowledge,
                            abilities, and experience necessary to perform a job.You can job-hunt by matching your skill
                            set to a certain profession, or enhance your skill set to further your career progression.</p>
                    </div>

                    <div className="col-md-5 bar-grids  w3-agile">
                        <h4> My Hobbies </h4>
                        <p></p>
                        {hobbyComponent}

                        <div className="clearfix"></div>
                        <h4> Programming skills </h4>
                        {programComponent}
                    </div>
                    <div className="clearfix"></div>
                </div>
            </div>
        </div>
    )
}

export default skillContainer