const WorkContainer = (props)=>{

    let workComponet;
    if(props.works){
        // console.log(this.state)
        workComponet = props.works.map(
            (work , index)=>{
                return <WhatIdo
                    key={index}
                    icon={work.icon}
                    name={work.name}
                    des = {work.description}
                />
            }
        )
    }

    return(
        <div className="services text-center pt-5 pb-lg-5" id="what"
             style={{background: "#e3e3e3", paddingBottom: "20px"}}>
            <div className="container py-xl-5 py-lg-3">
                <h3 className="tittle text-center text-bl mb-sm-5 mb-4">What i do ?</h3>
                <div className="row ser-styles ser-styles-res">
                    {workComponet}
                </div>
            </div>
        </div>
    )

}

export default WorkContainer;